.card {
    background: #fff;
    border-radius: 2px;
    height: 325px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .title {
    text-align: center;
    justify-content: center;
  }
  
  .name {
    text-transform: capitalize;
    text-align: center;
    font-weight: bold;
  }
  
  .container {
    width: 40%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
  }
  
  ul * {
    margin-top: auto;
  }
  
  .center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    padding-left: 20px;
  }
  